import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import { routes } from './routes';

Vue.use(VueRouter);

const router = new VueRouter({
    mode: 'history',
    routes
});

export const state = {};

export const eventBus = new Vue();

new Vue({
    el: '#app',
    router,
    render: h => h(App)
});


